<template lang="html">
  <div>
    <div class="edit" v-if="edit">
      <mavon-editor
        :toolbars="toolbars"
        :value="value"
        @change="val => $emit('change', val)"
      />
    </div>
    <div class="not-edit" v-else>
      <mavon-editor
        :toolbarsFlag="false"
        :subfield="false"
        defaultOpen="preview"
        :value="value"
      />
    </div>
  </div>
</template>

<script>
const toolbars = {
  bold: true, // 粗体
  italic: true, // 斜体
  header: true, // 标题
  underline: true, // 下划线
  strikethrough: true, // 中划线
  mark: true, // 标记
  superscript: true, // 上角标
  subscript: true, // 下角标
  quote: true, // 引用
  ol: true, // 有序列表
  ul: true, // 无序列表
  link: true, // 链接
  imagelink: false, // 图片链接
  code: true, // code
  table: true, // 表格
  fullscreen: true, // 全屏编辑
  readmodel: true, // 沉浸式阅读
  htmlcode: false, // 展示html源码
  help: true, // 帮助
  /* 1.3.5 */
  undo: true, // 上一步
  redo: true, // 下一步
  trash: true, // 清空
  save: false, // 保存（触发events中的save事件）
  /* 1.4.2 */
  navigation: true, // 导航目录
  /* 2.1.8 */
  alignleft: true, // 左对齐
  aligncenter: true, // 居中
  alignright: true, // 右对齐
  /* 2.2.1 */
  subfield: true, // 单双栏模式
  preview: true, // 预览
};

export default {
  name: 'MarkdownEditor',
  props: {
    edit: {
      type: Boolean,
      default: true,
    },
    value: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      toolbars,
    };
  },
};
</script>

<style lang="css" scoped>
.v-note-wrapper {
  max-height: 800px;
  z-index: 1;
}

.edit .v-note-wrapper {
  min-height: 500px;
}

.not-edit .v-note-wrapper {
  min-height: auto;
}
</style>



